.button-primary {
    color: var(--white);
    background-color: var(--buttonPrimary);
    box-shadow: 2px 2px 0px 1px var(--buttonPrimaryBorder);
}

.button-primary:hover {
    background-color: var(--buttonPrimaryHover);
    box-shadow: 2px 2px 0px 1px var(--buttonPrimaryBorderHover);
}

.button-primary:active {
    background-color: var(--buttonPrimary);
}

.button-primary:disabled {
    background-color: var(--buttonDisabled);
} 